  import axios from 'axios'
  const name = window.localStorage.getItem("userName")
  const myId = window.localStorage.getItem("myId")
	export default {
		data() {
			return {
        ye: "",
        name,
        myId,
        bind: false,
        aliZfb:''
			}
		},
		mounted() {
    // 查询支付宝账户
    axios
      .get('http://121.42.177.97:8080/red/pay/get_pay_phone.do?user_id=' + this.myId)
      .then(res => {
        console.log(res.data)
        if (res.data.data === null){
          this.bind = true
        }
        this.aliZfb = res.data.data
      })
      .catch(res => {
        throw res
      })
			//查询余额
			axios.post('http://121.42.177.97:8080/red/pay/money.do?user_name='+this.name)
				.then((response) => {
					console.log(response)
					if(response.data == "") {
						this.ye = "0"
					} else {
						this.ye = 0+response.data
					}
				})
				.catch(function(error) {
					console.log(error);
				});
		}
	}



